table, th, td {
    margin-top: 50px;
    border: 1px solid black;
    color:white;
    border-collapse: collapse;
  }

  th {
    padding: 5px;
    background-color:#e7a61a;
    color:black;
  }

  td {
    padding: 5px;
  }

  tr:hover {
    background-color:#e7a61a;
    colour:black;
  }

  button{
    width:40px;
    height:40px;
    background-color:#e7a61a;
    color:white;
  }
  .buttons button.active {
    background-color:black;
    color:white;
    
  }

  .buttons button:disabled {
    opacity: 0.4;
  }
